<template>
    <div>
        <v-form ref="form">
            <v-container>
                <v-row>
                    <v-col cols="12" md="3">
                        <v-text-field
                            type="email"
                            label="Email Address"
                            :rules="[rules.required, rules.emailAddress, rules.unique]"
                            dense
                            v-model="newEmail.emailAddress">
                        </v-text-field>
                    </v-col>
                    <v-col cols="12" md="3">
                        <v-text-field
                            type="password"
                            label="Password"
                            :rules="[rules.required]"
                            dense
                            v-model="newEmail.password">
                        </v-text-field>
                    </v-col>
                    <v-col cols="12" md="3">
                        <v-text-field
                            type="password"
                            label="Password Confirmation"
                            :rules="[rules.required, rules.samePassword]"
                            dense
                            v-model="newEmail.passwordConfirm">
                        </v-text-field>
                    </v-col>
                    <v-col cols="12" md="3">
                        <v-btn class="mx-2" fab dark small color="purple" @click.prevent="onSubmitClick">
                            <v-icon dark>mdi-plus</v-icon>
                        </v-btn>
                    </v-col>
                </v-row>
            </v-container>
        </v-form>
        <ul>
            <v-row
                v-for="email in this.emails"
                :key="email"
            >
                <v-list-item>
                    <v-col cols="9">
                        <v-list-item-content>
                            <span>{{email}}</span>
                        </v-list-item-content>
                    </v-col>
                    <v-col cols="3">
                        <v-list-item-icon @click="removeEmail(email)">
                                <v-icon>mdi-delete</v-icon>
                        </v-list-item-icon>
                    </v-col>
                    
                </v-list-item>
            </v-row>
        </ul>
    </div>
</template>

<script>
  export default {
    name: 'Add',
    data: () => ({
        emails: [],
        newEmail: {
            emailAddress: '',
            password: '',
            passwordConfirm: ''
        },
    }),
    computed: {
        rules() {
            return {
                required: value => !!value || "Required!",
                emailAddress: value => {
                    const regex = new RegExp(/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/);
                    if(typeof value === "string" && value.match(regex)) {
                        return true;
                    }
                    return "Not valid email address.";
                },
                unique: value => {
                    if(this.emails.indexOf(value) > -1) {
                        return "Email address already in use.";
                    }
                    return true;
                },
                samePassword: value => {
                    if( this.newEmail.password === value) {
                        return true;
                    }
                    return "Password should be the same.";
                }
            }
        }
    },
    methods: {
        onSubmitClick() {
            if(this.$refs.form.validate()) {
                // TODO send to api
                this.emails.push(this.newEmail.emailAddress);
                this.$refs.form.reset();
            }
        },

        removeEmail(emailAddress) {
            this.emails.splice(this.emails.indexOf(emailAddress), 1);
        }
    }
  };
</script>
